.createContent input[type="radio"] {
    display: inline-block;
    margin: -5px 12px 0 0;
    visibility: hidden;
}
.radio_label{
    display: inline-block;
    cursor: pointer;
    position: relative;
    /*  padding-left: 25px;*/
    margin-right: 15px;
}
.radio_label::before {
    content: "";
    display: inline-block;

    width: 10px;
    height: 10px;

    margin-right: 10px;
    position: absolute;
    left: 0;
    /*   bottom: 4px;*/
    border:1px solid rgb(255, 255, 255);
    /*background-color: #aaa;
    box-shadow: inset 0px 2px 3px 0px rgba(0, 0, 0, .3), 0px 1px 0px 0px rgba(255, 255, 255, .8);*/
    border-radius:100%;
}

.createContent input[type="radio"]:checked + span:before {
    content: "";
    display: inline-block;

    width: 11px;
    height: 11px;

    margin-right: 10px;
    position: absolute;
    left: 1px;
    top:1px;
    border-radius:50%;
    background-color: rgb(255, 255, 255);
    border: none;
}

