  .dropdown {
    margin-left:0px ;
    width: 50px;
    display: inline-block;
  }
  
  .dropdown-content {
    margin-left:0px ;
    display: none;
    position: absolute; 
    width: 150px;
    margin-top: 10px;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    font-size: 13px;
  }


  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    border-radius: 10px;
  }
  
  
  .dropdown-content a:hover {background-color: #f1f1f1}
  
  .dropdown:hover .dropdown-content {
    display: block;
    border-radius: 10px;
  }
  
